.map-viewbox {
  border: 2.5px white dotted;
  width: calc(100% - 45px);
  height: 50%;
  padding: 20px;
  border-radius: 15px;

}

.model-select {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
}

.model-select h2 {
  margin-bottom: 0;
}

.model-select .button-container {
  border-top: 3px white dotted;
  width: 90%;
  margin-bottom: 10px;
  padding-top: 5px;
}

.model-select .button-container button {
  background-color: var(--maincolor);
  color: white;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 1rem;
  border: 0;
  padding: 5px 15px;
  margin: 5px;
  border-radius: 15px;
  box-shadow: 0px -3px 1px var(--main2tint) inset,
    0px 1px 1px rgba(255, 255, 255, 0.1) inset;
  transition: 0.2s;
}

.model-select .button-container button:hover {
  box-shadow: 0px 3px 1px var(--main2tint) inset,
    0px 1px 1px rgba(255, 255, 255, 0.1);
  transition: 0.2s;
}

.model-select .button-container button:hover p {
  transform: translateY(3px);
  transition: 0.2s;
}

.model-select #pres {
  display: flex;
  flex-direction: column;
  width: 88%;
}

.model-select #model button,
.model-select #results button {
  width: 45%;
}